const slider = document.querySelector('[data-slider="wash"]');

const initBannerSlider = () => {
  if (!slider) {
    return;
  }

  const swiper = new Swiper(slider, {
    slidesPerView: 'auto', 
  
    spaceBetween: 20,
    speed: 800,
    breakpoints: {
      
      300: {
        spaceBetween: 15,
      },
      
      520: {
        spaceBetween: 20,
      },
    },
      
    navigation: {
      nextEl: '.swiper-button-next.wash-next',
      prevEl: '.swiper-button-prev.wash-prev',
    }
  });
};

export { initBannerSlider }; 